.movieLayer{width: 30%; height: 350px; float: left; margin: 30px; box-shadow: -2px 3px 0 0 #eaeaea;}
.movieLayer h2{background: #2522AD; padding: 5px; height: 32px; box-shadow: -3px 0 0 0 #ddd;}
.movieLayer h2 a{color:#fff; text-decoration: none;}
.movieLayer .leftArea, .movieLayer .rightArea{float: left; height: calc(100% - 42px);}
.movieLayer .leftArea{width: 30%;}
.movieLayer .rightArea{width: 68%; padding: 5px;}
.movieLayer .rightArea p{margin: 8px 0; color: #2522AD; font-weight: 700;}
.movieLayer .rightArea p::before{content: " - ";}
.movieLayer .rightArea p.rating{color: #FF5E00;}
.movieLayer .leftArea img, .movieLayer .rightArea img{width: 100%;}
.movieLayer .posterBox{position: relative; top: 55%; left: 40%; transform: translate(-55%, -55%); padding: 5px; width: 90%; height: auto; box-shadow: -2px 3px 0 0 #ddd; background-color: #FF5E00;}
.movieLayer .summary{background-color: #f9fbff; color: #000;}
.movieLayer .summary::before{content: "\"";}
.movieLayer .summary::after{content: "\"";}